.landing-container {
    display: flex;
    min-height: 90vh;
    justify-content: space-between;
    align-items: center;
    color: #eee2ee;
    padding: 0 32px;
    font-family: "Bangers", cursive;
    margin: 0;
    flex-wrap: wrap;
}
.landing-header {
    font-size: 75px;
    margin: 0;
    letter-spacing: 3px;
}
.typewriter-container {
    font-size: 40px;
    letter-spacing: 2px;
}
.flash-image {
    width: 40vw;
}

@media (max-width: 800px) {
    .landing-container {
        flex-direction: column;
        align-items: center;
    }

    .flash-image {
        width: 80vw;
    }
    .landing-header {
        margin-top: 32px;
        text-align: center;
    }
    .typewriter-container {
        text-align: center;
    }
}
